<template>
  <div class="add-customer-page">
    <div class="m-header clear-float">
      <div class="title-text">{{type=='add'?'':''}}</div>
      <div class="icon-box">
        <router-link to='/CustomerList'>{{type=='add'?'':''}}</router-link>
      </div>
    </div>
    <div class="add-customer-content">
      <div class="message-box">
        <div class="message-title"></div>
        <div class="message-conetnt"><input @blur.prevent="blur()" v-model="customerName" placeholder=""/></div>
      </div>
      <div class="message-box" @click="popupVisible = true">
        <div class="message-title"></div>
        <div class="message-conetnt">{{customerLevel}}</div>
      </div>
      <div class="message-box">
        <div class="message-title"></div>
        <div class="message-conetnt"><mt-switch v-model="targetCustomer"></mt-switch></div>
      </div>
      <div class="message-box" @click="isPicker = true">
        <div class="message-title"></div>
        <div class="message-conetnt">{{initial.first}}{{initial.second}}{{initial.third}}</div>
      </div>
      <div class="message-box">
        <div class="message-title"></div>
        <div class="message-conetnt"><input @blur.prevent="blur()" v-model="address" placeholder=""/></div>
      </div>
      <div class="message-box n-border">
        <div class="message-title"></div>
        <div class="message-conetnt c-blue" @click="addPeoplePopupVisible = true"></div>
      </div>
      <div class="contacts-list">
        <div class="contacts-card">
          <div class="contacts-top">
            <div class="contacts-left">
              <div>---</div>
              <div>159-213-0045</div>
            </div>
            <div class="call-image"></div>
          </div>
          <div class="contacts-bottom">
            <div class="bottom-left"><mt-switch v-model="defaultContact"></mt-switch></div>
            <div class="bottom-right"><div @click="addPeoplePopupVisible = true"></div> | <div></div></div>            
          </div>
        </div>
      </div>
    </div>
    <mt-popup class="popup-style" v-model="popupVisible" position="bottom">
      <div class="picker-toolbar">
        <span class="mint-datetime-action mint-datetime-cancel" @click="popupVisible = false"></span>
        <span class="mint-datetime-action mint-datetime-confirm" @click="popupVisible = false"></span>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </mt-popup>
    <!-- / -->
    <mt-popup v-model="addPeoplePopupVisible" position="right" class="add-people-popup-follow" :modal="false">
      <div class="m-header clear-float"><div class="title-text"></div></div>
      <div class="content">
        <div class="message-box">
          <div class="message-title"></div>
          <div class="message-conetnt"><input @blur.prevent="blur()" placeholder=""/></div>
        </div>
        <div class="message-box" @click="sexPopupVisible = true">
          <div class="message-title"></div>
          <div class="message-conetnt">{{newPeopleSex}}</div>
        </div>
        <div class="message-box">
          <div class="message-title"></div>
          <div class="message-conetnt"><input @blur.prevent="blur()" placeholder=""/></div>
        </div>
        <div class="message-box">
          <div class="message-title"></div>
          <div class="message-conetnt"><input @blur.prevent="blur()" placeholder=""/></div>
        </div>
        <div class="message-box">
          <div class="message-title"></div>
          <div class="message-conetnt"><input @blur.prevent="blur()" placeholder=""/></div>
        </div>
      </div>
      <mt-popup class="popup-style" v-model="sexPopupVisible" position="bottom">
        <div class="picker-toolbar">
          <span class="mint-datetime-action mint-datetime-cancel" @click="sexPopupVisible = false"></span>
          <span class="mint-datetime-action mint-datetime-confirm" @click="sexPopupVisible = false"></span>
        </div>
        <mt-picker :slots="sexSlots" @change="changeSex"></mt-picker>
      </mt-popup>
      <div class="commit-btn" @click="addPeoplePopupVisible= false"></div>
    </mt-popup>
    <CascadingPicker 
        v-model="isPicker"
        :data="addressData"
        :initial="initial"
        closeOnClickModal
        @ok="selectAddress"
        @cancel="cancelAddress"
    />
  </div>
</template>

<script>
import axs from "../../axios.service.js";
import Vue from 'vue'
import CascadingPicker from 'cascading-picker'
import 'cascading-picker/dist/CascadingPicker.css'
import addressData from 'cascading-picker/dist/AddressData'
Vue.use(CascadingPicker);
export default {
  name: "AddCustomer",
  data() {
    return {
      type:this.$route.query.type || 'add',// 
      customerName:'',//
      customerLevel:'',//
      popupVisible:false,//
      slots:[{values: ['',''],defaultIndex:0}], 
      targetCustomer:false,//
      address:'',//
      defaultContact:false,//
      addPeoplePopupVisible:false,//
      sexPopupVisible:false,//
      sexSlots:[{values: ['',''],defaultIndex:0}], 
      newPeopleSex:'',
      isPicker: false,
      addressData: addressData,
      initial: {
          first: '',
          second: '',
          third: ''
      }
    };
  },
  methods:{
    blur:function(){scrollTo(0, pageYOffset)},
    // 
    onValuesChange(picker, values) {
      this.customerLevel = values[0];
    },
    // 
    changeSex(picker, values){
      this.newPeopleSex = values[0];
    },
    // 
    selectAddress(res) {
      this.addressProvice = res.first+','+res.second+','+res.third;
      this.initial.first = res.first;
      this.initial.second = res.second;
      this.initial.third = res.third;
    },
    // 
    cancelAddress() {   
      console.log('')
    }
  },
  created(){

  }
};
</script>

<style lang="scss" scoped>
.add-customer-page{
  font-size: 28px;
  .message-box{
    width: 92%;
    margin:0 auto;
    height: 90px;
    line-height: 90px;
    border-bottom:1px solid #e5e5e5;
    .message-title{display: inline-block}
    .message-conetnt{
      float: right;width: 50%;text-align: right;
      input{text-align: right;font-size: 28px;}
    }
    .mint-switch{float: right;margin-top: 22.5px;}
  }
  .n-border{border-bottom:none;}
  .contacts-list{
    width: 92%;
    margin: 0 auto;
    .contacts-card{
      border: 2px solid #e5e5e5;
      border-radius: 20px;
      margin-bottom: 25px;
      .contacts-top{
        line-height: 80px;
        font-size: 26px;
        border-bottom: 1px solid #e5e5e5;
        .contacts-left{width: 60%;margin: 0 5%;display: inline-block;}
        .call-image{
          float: right;
          background: url('../../../static/image/dianhua.png')no-repeat;
          background-size: 100% 100%;
          width: 64px;
          height: 64px;
          margin: 48px;
        }
      }
      .contacts-bottom{
        height: 80px;
        line-height: 80px;
        .bottom-left{
          width: 60%;
          margin:0 5%;
          display: inline-block;
        }
        .bottom-right{float: right;margin-right:5%; div{display: inline-block}}
      }
    }
  }
  .popup-style{width: 100%;}
  .popup-style .sex-btn-box{text-align: right;padding: 3px 10px;}
}
.add-people-popup-follow{
  width: 100%;
  height: 100%;
  background-color: #fff;
  .content{
    font-size: 30px;
    .message-box{
      width: 92%;
      margin:0 auto;
      height: 90px;
      line-height: 90px;
      border-bottom:1px solid #e5e5e5;
      .message-title{display: inline-block}
      .message-conetnt{
        float: right;width: 50%;text-align: right;
        input{text-align: right;font-size: 28px;}
      }
    }
  }
}


</style>



